<template>
    <div>
        <b-container fluid class="p-4">
            <b-row class="mb-4">
                <b-col lg="6">
                    <b-form-group class="mb-0">
                        <b-input-group size="sm">
                            <b-form-input
                                id="filter-input"
                                v-model="filter"
                                type="search"
                                placeholder="Type to Search"
                            ></b-form-input>

                            <b-input-group-append>
                                <b-button
                                    class="rounded-0 rounded-end"
                                    :disabled="!filter"
                                    @click="filter = ''"
                                    >Clear</b-button
                                >
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col lg="6">
                    <b-table
                        id="table"
                        :items="items"
                        :fields="fields"
                        :per-page="perPage"
                        :current-page="currentPage"
                        :busy="isBusy"
                        ::sort-by.sync="sortBy"
                        ::sort-desc.sync="sortDesc"
                        sort-icon-left
                        bordered
                        responsive
                        show-empty
                        :filter="filter"
                        @filtered="onFiltered"
                    >
                        <template #cell(name)="data">
                            <name-cell @changeRow="onChangeRow" :data="data" />
                        </template>

                        <template #cell(actions)="row">
                            <b-button variant="danger" @click="deleteRow(row.item)">
                                Удалить
                            </b-button>
                        </template>

                        <template #table-busy>
                            <div class="text-center my-2">
                                <b-spinner class="align-middle me-2"></b-spinner>
                                <strong>Загрузка...</strong>
                            </div>
                        </template>
                    </b-table>
                </b-col>
            </b-row>
            <b-row>
                <b-col lg="6">
                    <b-row>
                        <b-col>
                            <b-pagination
                                v-model="currentPage"
                                :total-rows="rows"
                                :per-page="perPage"
                                first-number
                                last-number
                                aria-controls="table"
                            ></b-pagination>
                        </b-col>
                        <b-col cols="3" class="text-end">
                            <simple-add-row @addNewRow="onAddNewRow" title="Добавление записи" />
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
import axios from "axios";

import NameCell from "./NameCell.vue";
import SimpleAddRow from "./SimpleAddRow.vue";
// Loading anim
// https://bootstrap-vue.org/docs/components/table#table-busy-state

export default {
    components: { NameCell, SimpleAddRow },
    props: ["name"],
    data() {
        return {
            perPage: 10,
            currentPage: 1,
            sortBy: "name",
            sortDesc: false,
            fields: [
                { key: "name", sortable: false },
                { key: "actions", label: "Actions" },
            ],
            items: [],
            filter: null,
            isBusy: false,
        };
    },
    methods: {
        fetchData() {
            this.isBusy = true;
            axios
                .get(`http://localhost:5000/api/v0.1/get/${this.name}`)
                .then((res) => {
                    this.items = res.data;
                    this.isBusy = false;
                })
                .catch((err) => this.showErrorToast(err));
        },
        showErrorToast(err) {
            console.log(err);
            this.$bvToast.toast("Произошла ошибка при загрузке данных!", {
                title: "Ошибка",
                autoHideDelay: 5000,
                variant: "danger",
            });
        },
        resetInfoModal() {
            this.infoModal.title = "";
            this.infoModal.content = "";
        },
        onFiltered(filteredItems) {
            this.rows = filteredItems.length;
            this.currentPage = 1;
        },
        onChangeRow(data) {
            this.items.find((x) => x.id === data.id).name = data.name;
        },
        onAddNewRow(data) {
            this.items.push({
                id: parseInt(Math.random() * 100),
                name: data.name,
                first_name: "test",
            });
            console.table(this.items);
        },
        deleteRow(item) {
            const index = this.items.indexOf(this.items.find((x) => x.id === item.id));
            this.items.splice(index, 1);
        },
    },
    computed: {
        rows() {
            return this.items.length;
        },
    },
    mounted() {
        this.fetchData();
    },
    watch: {
        name: function (val) {
            console.log(val);
            this.fetchData();
        },
    },
};
</script>
