<template>
    <keep-alive>
        <div>
            <Title :title="name" exim="true" />
            <div v-if="mode === 'extended'">
                <advanced-table :name="name" />
            </div>
            <div v-else>
                <simple-table :name="name" />
            </div>
        </div>
    </keep-alive>
</template>

<script>
import Title from "@/components/Title.vue";
import SimpleTable from "@/components/tables/SimpleTable.vue";
import AdvancedTable from "../components/tables/AdvancedTable.vue";

export default {
    components: { Title, SimpleTable, AdvancedTable },
    props: ["name", "id"],
    data() {
        return {
            title: "__DEV",
        };
    },
    computed: {
        mode() {
            if (this.name == "providers" || this.name == "products") {
                return "extended";
            }
            return "simple";
        },
    },
};
</script>

