<template>
    <div>
        <b-button variant="primary" @click="showAddModal($event.target)"> Добавить </b-button>

        <b-modal :id="addModal.id" :title="addModal.title" @hide="resetAddModal" centered>
            <b-row>
                <b-col class="position-relative">
                    <label
                        class="position-absolute top-50 start-50 translate-middle"
                        for="input-default"
                        >Наименование:</label
                    >
                </b-col>
                <b-col cols="8">
                    <b-form-input
                        v-model="addModal.name"
                        id="input-default"
                        placeholder="Введите текст"
                    ></b-form-input>
                </b-col>
            </b-row>

            <template #modal-footer="{ ok, cancel }">
                <b-button variant="danger" @click="cancel()"> Отмена </b-button>
                <b-button variant="success" @click="addNewRow(ok)"> OK </b-button>
            </template>
        </b-modal>
    </div>
</template>

<script>
export default {
    props: ["title"],
    data() {
        return {
            addModal: {
                title: this.title,
                id: "add-row-modal",
                name: "",
            },
        };
    },
    methods: {
        showAddModal(button) {
            this.$bvModal.show(this.addModal.id, button);
        },
        resetAddModal() {
            //   this.addModal.title = "";
            this.addModal.name = "";
        },
        addNewRow(ok) {
            this.$emit("addNewRow", { name: this.addModal.name });
            ok();
        },
    },
};
</script>
