<template>
    <div>
        <b-container fluid class="p-4">
            <!-- Live Search -->
            <b-row class="mb-4">
                <b-col lg="6">
                    <b-form-group class="mb-0">
                        <b-input-group size="sm">
                            <b-form-input
                                id="filter-input"
                                v-model="filter"
                                type="search"
                                placeholder="Type to Search"
                            ></b-form-input>

                            <b-input-group-append>
                                <b-button
                                    class="rounded-0 rounded-end"
                                    :disabled="!filter"
                                    @click="filter = ''"
                                    >Clear</b-button
                                >
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
            </b-row>
            <!-- Child Window Btn & Filters -->
            <b-row class="mb-4">
                <b-col lg="6">
                    <b-row>
                        <b-col>
                            <!-- <b-button> Города </b-button> -->
                            <b-row>
                                <b-col v-for="item in filtersList" :key="item.id" cols="12 mb-2">
                                    <router-link
                                        :to="{
                                            name: 'dict',
                                            params: {
                                                name: item.key,
                                                title: item.multiple,
                                            },
                                        }"
                                    >
                                        <b-button>{{ item.multiple }}</b-button>
                                    </router-link>
                                </b-col>
                            </b-row>
                        </b-col>
                        <b-col cols="8" class="text-end">
                            <b-row>
                                <b-col v-for="item in filtersList" :key="item.id" cols="12 mb-2">
                                    <filter-modal :label="item.label" :model="item.key" />
                                </b-col>
                            </b-row>
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
            <!-- Table -->
            <b-row>
                <b-col lg="6">
                    <b-table
                        id="table"
                        :items="items"
                        :fields="fields"
                        :per-page="perPage"
                        :current-page="currentPage"
                        :busy="isBusy"
                        ::sort-by.sync="sortBy"
                        ::sort-desc.sync="sortDesc"
                        sort-icon-left
                        bordered
                        responsive
                        show-empty
                        :filter="filter"
                        @filtered="onFiltered"
                    >
                        <template #cell(name)="data">
                            <name-cell @changeRow="onChangeName" :data="data" />
                        </template>

                        <template #cell()="data">
                            <span v-if="data.item.city">
                                <change-field-modal
                                    @selectRow="onSelectRow"
                                    :id="data.item.id"
                                    :label="data.item.city"
                                    model="city"
                                />
                            </span>

                            <span v-else> {{ data.item.value }} # </span>
                        </template>

                        <template #cell(actions)="row">
                            <span v-for="item in actionList" :key="item.id" class="me-2">
                                <router-link
                                    v-if="item.action == 'route'"
                                    :to="item.to + '/' + row.item.id"
                                    tag="b-button"
                                >
                                    {{ item.label }}
                                </router-link>
                            </span>
                            <b-button
                                v-if="item.action == 'delete'"
                                variant="danger"
                                @click="deleteRow(row.item)"
                            >
                                {{ item.label ? item.label : "Удалить" }}
                            </b-button>
                        </template>

                        <template #table-busy>
                            <div class="text-center my-2">
                                <b-spinner class="align-middle me-2"></b-spinner>
                                <strong>Загрузка...</strong>
                            </div>
                        </template>
                    </b-table>
                </b-col>
            </b-row>
            <!-- Pagination & Add New Row Btn -->
            <b-row>
                <b-col lg="6">
                    <b-row>
                        <b-col>
                            <b-pagination
                                v-model="currentPage"
                                :total-rows="rows"
                                :per-page="perPage"
                                first-number
                                last-number
                                aria-controls="table"
                            ></b-pagination>
                        </b-col>
                        <b-col cols="3" class="text-end">
                            <simple-add-row @addNewRow="onAddNewRow" title="Добавление записи" />
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
import axios from "axios";

import ChangeFieldModal from "./ChangeFieldModal.vue";
import FilterModal from "./FilterModal.vue";
import NameCell from "./NameCell.vue";
import SimpleAddRow from "./SimpleAddRow.vue";
// Loading anim
// https://bootstrap-vue.org/docs/components/table#table-busy-state

// sticky-header

export default {
    components: { NameCell, SimpleAddRow, FilterModal, ChangeFieldModal },
    props: ["name"],
    data() {
        return {
            perPage: 10,
            currentPage: 1,
            sortBy: "city",
            sortDesc: false,
            filter: null,
            isBusy: false,
            items: [],
        };
    },
    computed: {
        rows() {
            return this.items.length;
        },
        fields() {
            return [
                { key: "name", label: "Наименование", sortable: false },
                { key: "city", label: "Город", sortable: false },
                { key: "actions", label: "Действия" },
            ];
        },
        filtersList() {
            return [
                {
                    key: "city",
                    label: "Город",
                    multiple: "Города",
                },
                {
                    key: "city",
                    label: "Город",
                    multiple: "Города",
                },
                {
                    key: "city",
                    label: "Город",
                    multiple: "Города",
                },
            ];
        },
        actionList() {
            return [
                {
                    label: "Товары",
                    action: "route",
                    to: "products",
                },
                {
                    label: "Удалить",
                    action: "delete",
                },
            ];
        },
    },
    methods: {
        fetchData() {
            this.isBusy = true;
            axios
                .get(`http://localhost:5000/api/v0.1/get/${this.name}`)
                .then((res) => {
                    this.items = res.data;
                    this.isBusy = false;
                })
                .catch((err) => this.showErrorToast(err));
        },
        showErrorToast(err) {
            console.log(err);
            this.$bvToast.toast("Произошла ошибка при загрузке данных!", {
                title: "Ошибка",
                autoHideDelay: 5000,
                variant: "danger",
            });
        },
        resetInfoModal() {
            this.infoModal.title = "";
            this.infoModal.content = "";
        },
        onFiltered(filteredItems) {
            this.rows = filteredItems.length;
            this.currentPage = 1;
        },
        onChangeName(item) {
            this.items.find((x) => x.id === item.id).name = item.name;
        },
        onSelectRow(data) {
            this.items.find((x) => x.id === data.id).city = data.name;
        },
        onAddNewRow(item) {
            this.items.push({
                id: parseInt(Math.random() * 100),
                name: item.name,
                city: "test",
            });
            console.table(this.items);
        },
        deleteRow(item) {
            const index = this.items.indexOf(this.items.find((x) => x.id === item.id));
            this.items.splice(index, 1);
        },
    },
    mounted() {
        this.fetchData();
    },
    watch: {
        name: function () {
            this.fetchData();
        },
    },
};
</script>
